import React from 'react';
import { hot } from 'react-hot-loader';
import { Switch, Route } from 'react-router-dom';
import Loadable from 'react-loadable';
import { routerPaths } from './helper';
import ErrorBoundary from '../components/ErrorBoundary';



const delay = 200;

const HomePage = Loadable({
  loader: () => import('../pages/HomePage'),
  loading: () => null,
  delay,
});

const NftHeroesPage = Loadable({
  loader: () => import('../pages/NftHeroesPage'),
  loading: () => null,
  delay,
});
const NftItemsPage = Loadable({
  loader: () => import('../pages/NftItemsPage'),
  loading: () => null,
  delay,
});

const NftMixerPage = Loadable({
  loader: () => import('../pages/NftMixerPage'),
  loading: () => null,
  delay,
});
const NftMarketplacePage = Loadable({
  loader: () => import('../pages/NftMarketplacePage'),
  loading: () => null,
  delay,
});
const LotteryPage = Loadable({
  loader: () => import('../pages/LotteryPage'),
  loading: () => null,
  delay,
});

const NftStakingPage = Loadable({
  loader: () => import('../pages/NftStakingPage'),
  loading: () => null,
  delay,
});
const TokenFarmingPage = Loadable({
  loader: () => import('../pages/TokenFarmingPage'),
  loading: () => null,
  delay,
});


const ContractsPage = Loadable({
  loader: () => import('../pages/ContractsPage'),
  loading: () => null,
  delay,
});
const FaqPage = Loadable({
  loader: () => import('../pages/FaqPage'),
  loading: () => null,
  delay,
});

const TermsPage = Loadable({
  loader: () => import('../pages/TermsPage'),
  loading: () => null,
  delay,
});
const PrivacyPage = Loadable({
  loader: () => import('../pages/PrivacyPage'),
  loading: () => null,
  delay,
});

const NotFoundPage = Loadable({
  loader: () => import('../pages/NotFoundPage'),
  loading: () => null,
  delay,
});


function AppRouter() {
  return (
    <ErrorBoundary>
      <Switch>
        <Route exact path={routerPaths.main} component={HomePage} />

        <Route exact path={routerPaths.heroes} component={NftHeroesPage} />
        <Route exact path={routerPaths.items} component={NftItemsPage} />
        <Route exact path={routerPaths.mixer} component={NftMixerPage} />
        <Route exact path={routerPaths.marketplace} component={NftMarketplacePage} />
        <Route exact path={routerPaths.lottery} component={LotteryPage} />

        <Route exact path={routerPaths.staking} component={NftStakingPage} />
        <Route exact path={routerPaths.farming} component={TokenFarmingPage} />

        <Route exact path={routerPaths.contacts} component={ContractsPage} />
        <Route exact path={routerPaths.faq} component={FaqPage} />

        <Route exact path={routerPaths.terms} component={TermsPage} />
        <Route exact path={routerPaths.privacy} component={PrivacyPage} />

        <Route component={NotFoundPage} />
      </Switch>
    </ErrorBoundary>
  );
}

export default hot(module)(AppRouter);
